.site-footer {
  background-color: var(--light-blue);
}

.site-footer-navigation {
  display: grid;
  align-items: center;
  height: 100%;

  grid-template-columns: 300px 1fr 300px;
}

// Footer logo
.footer-logo img {
  display: block;
  width: 97px;
}

.site-footer .menu {
  margin: auto;
}

.academy-link {
  text-align: right;
}
