.catalog {
  padding-top: 100px;
  padding-bottom: 136px;

  background-color: var(--light-blue);
}

.catalog__container {
  & > h2,
  & > h3 {
    justify-content: center;
    width: 100%;

    text-align: center;
  }

  h3 {
    margin-bottom: 40px;

    font-weight: bold;
    font-size: 32px;
    line-height: 1.5;
    color: var(--color);
  }
}

.houses-list {
  display: grid;

  gap: 24px;
  grid-template-columns: repeat(12, 1fr);
}

.house {
  display: grid;

  background: #ffffff;
  border: 1px solid #cfdce3;

  grid-column: span 3;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 220px auto;
}

.house__image {
  grid-column: 1 / -1;

  img {
    width: 100%;
    height: 100%;

    object-fit: cover;
    object-position: center;
  }
}

.house__text {
  padding: 16px 24px 24px;

  grid-column: 1 / -1;

  h4 {
    font-weight: 400;
    font-size: 22px;
    line-height: 34px;
    font-family: var(--ff);
    color: var(--color);
  }

  p {
    margin-bottom: 20px;

    line-height: 20px;
    color: #333333;

    opacity: 0.3;
  }

  dl {
    display: grid;
    padding-top: 16px;

    font-weight: 300;
    color: #333333;
    column-gap: 50px;

    border-top: 2px solid #eaf2f6;

    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, auto);

    dt {
      font-size: 16px;
      line-height: 20px;

      grid-row: 2 / 3;
    }

    dd {
      font-size: 32px;
      line-height: 36px;
    }
  }
}


.house--wide {
  grid-column: 3 / -3;
  grid-template-columns: 326px auto;
  grid-template-rows: 198px auto;

  .house__image {
    grid-column: 1 / 2;
  }

  .house__text {
    grid-column: 2 / -1;
  }
}
