.site-header {
  background: var(--light-blue);
}

.site-nav {
  display: grid;
  align-items: center;
  height: 100%;

  grid-template-columns: 1fr 210px 1fr;
}

// Menu
.menu {
  display: flex;

  li {
    margin-right: 64px;

    &:last-child {
      margin-right: 0;
    }
  }
}

.menu__link {
  font-size: var(--fz);
  line-height: 20px;
  color: var(--accent-color);
  text-decoration: none;

  &:hover,
  &:focus,
  &:active {
    text-decoration: underline;
  }
}

// Logo
.logo {
  margin: -11px auto 0;

  img {
    display: block;
    max-width: 200px;
  }
}

// User controls: search, log in, sign in
.user-controls {
  display: flex;
  justify-content: flex-end;
}

// Search
.search__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  padding: 0;

  color: var(--accent-color);

  background: transparent;
  border: none;
  cursor: pointer;

  &:hover {
    color: var(--color);
  }

  svg {
    width: 16px;
    height: 16px;

    fill: currentColor;
  }
}

// Log in and Sign in
.login-links {
  margin-left: 56px;
}
