.slider {
  position: relative;
  grid-column: 6 / -1;
  grid-row: 1 / -1; }
  .slider img {
    object-fit: cover;
    object-position: center; }

.slider__controls {
  position: absolute;
  bottom: 0;
  display: grid;
  width: 100%;
  height: 64px;
  grid-template-columns: auto auto 1fr; }

.slider-button {
  width: 104px;
  height: 64px;
  padding: 21px 39px;
  color: #eaf2f6;
  background: #ffffff;
  border: none;
  cursor: pointer; }
  .slider-button:hover, .slider-button:focus, .slider-button:active {
    color: var(--accent-color); }
  .slider-button svg {
    width: 100%;
    height: 100%;
    stroke: currentColor; }
  .slider-button:first-child {
    position: relative; }
    .slider-button:first-child::before {
      content: "";
      position: absolute;
      top: 16px;
      left: 104px;
      z-index: 20;
      width: 2px;
      height: 32px;
      background-color: #eaf2f6; }

.slider-counter {
  padding-right: 30px;
  font-size: 32px;
  line-height: 1.5;
  text-align: right;
  color: #ffffff; }

.place {
  padding-top: 99px; }

.place__container {
  display: grid;
  padding-bottom: 75px;
  column-gap: 24px;
  grid-template-columns: repeat(12, 1fr); }
  .place__container > * {
    margin-bottom: 24px;
    grid-column: 5 / 9; }

.section-title {
  display: flex;
  align-items: center;
  margin-bottom: 60px;
  font-weight: 300;
  font-size: var(--fz);
  line-height: 1.25;
  color: var(--accent-color); }
  .section-title .divider {
    width: 83px;
    height: 1px;
    margin: 0 9px;
    background-color: #06a9b3; }

.place__container h3 {
  font-weight: bold;
  font-size: 32px;
  line-height: 1.5;
  color: var(--color); }

.place__container .place-wide-img {
  grid-column: 2 / -2; }

.place__container blockquote {
  margin-bottom: 33px;
  padding-top: 9px;
  padding-left: 28px;
  font-size: 22px;
  line-height: 40px;
  color: #333333;
  border-left: 2px solid var(--accent-color);
  grid-column: 4 / 10; }

.place-aside {
  font-weight: 300;
  font-size: 14px;
  line-height: 24px; }
  .place-aside--right {
    grid-column: 10 / 12; }
  .place-aside--left {
    grid-column: 2 / 4; }

.place__action {
  width: 289px;
  height: 52px;
  margin-top: 8px;
  padding: 16px 32px;
  font-weight: 500;
  line-height: 1.25;
  color: #ffffff;
  background: var(--accent-color); }
  .place__action:hover, .place__action:active, .place__action:focus {
    text-decoration: none;
    opacity: 0.8; }

.catalog {
  padding-top: 100px;
  padding-bottom: 136px;
  background-color: var(--light-blue); }

.catalog__container > h2,
.catalog__container > h3 {
  justify-content: center;
  width: 100%;
  text-align: center; }

.catalog__container h3 {
  margin-bottom: 40px;
  font-weight: bold;
  font-size: 32px;
  line-height: 1.5;
  color: var(--color); }

.houses-list {
  display: grid;
  gap: 24px;
  grid-template-columns: repeat(12, 1fr); }

.house {
  display: grid;
  background: #ffffff;
  border: 1px solid #cfdce3;
  grid-column: span 3;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 220px auto; }

.house__image {
  grid-column: 1 / -1; }
  .house__image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center; }

.house__text {
  padding: 16px 24px 24px;
  grid-column: 1 / -1; }
  .house__text h4 {
    font-weight: 400;
    font-size: 22px;
    line-height: 34px;
    font-family: var(--ff);
    color: var(--color); }
  .house__text p {
    margin-bottom: 20px;
    line-height: 20px;
    color: #333333;
    opacity: 0.3; }
  .house__text dl {
    display: grid;
    padding-top: 16px;
    font-weight: 300;
    color: #333333;
    column-gap: 50px;
    border-top: 2px solid #eaf2f6;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, auto); }
    .house__text dl dt {
      font-size: 16px;
      line-height: 20px;
      grid-row: 2 / 3; }
    .house__text dl dd {
      font-size: 32px;
      line-height: 36px; }

.house--wide {
  grid-column: 3 / -3;
  grid-template-columns: 326px auto;
  grid-template-rows: 198px auto; }
  .house--wide .house__image {
    grid-column: 1 / 2; }
  .house--wide .house__text {
    grid-column: 2 / -1; }

.contacts {
  display: grid;
  column-gap: 24px;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: 230px auto; }

.contacts__title,
.contacts__form {
  grid-column: 2 / 6; }

.contacts__img {
  grid-column: 7 / 13;
  grid-row: 1 / -1; }

.contacts__title .section-title {
  padding-top: 100px; }

.contacts__title h3 {
  margin-bottom: 40px;
  font-weight: bold;
  font-size: 32px;
  line-height: 1.5;
  color: var(--color); }

.contacts__form {
  margin-top: 14px; }

.form {
  display: grid;
  gap: 10px 24px;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(5, auto); }

.form__element:nth-child(1), .form__element:nth-child(2), .form__element:nth-child(3) {
  grid-column: span 2; }

.form__element:nth-child(4), .form__element:nth-child(5) {
  grid-column: span 6; }

.form__element:nth-child(6), .form__element:nth-child(7) {
  grid-column: span 3; }

.form__element:nth-child(8) {
  grid-column: span 6; }

.form__element.checkbox {
  margin-top: 20px; }

.form__element input,
.form__element select {
  width: 100%;
  height: 44px;
  padding: 0 16px;
  background: #ffffff;
  border: 1px solid #cfdce3; }
  .form__element input:focus,
  .form__element select:focus {
    outline: 2px solid var(--accent-color); }

.form__element label {
  font-weight: 300;
  font-size: 12px;
  line-height: 1; }

.form__submit {
  width: 155px;
  height: 44px;
  margin-top: 30px;
  padding: 11px 32px;
  font-weight: 500;
  line-height: 1.25;
  color: #ffffff;
  background: var(--accent-color);
  border: none; }
  .form__submit:hover, .form__submit:active, .form__submit:focus {
    text-decoration: none;
    opacity: 0.8; }

.checkbox label {
  position: relative;
  display: flex;
  padding-left: 30px;
  font-weight: 300;
  font-size: 12px;
  line-height: 16px;
  color: #333333; }
  .checkbox label::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 18px;
    height: 18px;
    border: 1px solid #cfdce3; }
  .checkbox label::after {
    position: absolute;
    top: 3px;
    left: 3px;
    width: 12px;
    height: 12px;
    background: var(--accent-color); }

.checkbox input[type="checkbox"]:checked + label::after {
  content: ""; }
