// Layout styles

.site-page {
  display: grid;
  min-height: 100vh;

  grid-template-areas: "header"
  "main"
  "footer";
  grid-template-rows: 80px 1fr 100px;
}

.container {
  max-width: 1380px;
  margin: 0 auto;
}

.side-header {
  grid-area: header;
}

.side-main {
  grid-area: main;
}

.side-footer {
  grid-area: footer;
}
