.welcome {
  padding-bottom: 100px;

  background-color: var(--light-blue);
}

.welcome__container {
  display: grid;

  column-gap: 24px;

  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: 100px auto auto 180px 64px;
}

.welcome__city {
  align-self: flex-end;
  padding-top: 27px;

  font-weight: 400;
  font-size: 16px;
  line-height: 1.25;
  color: #333333;

  opacity: 0.3;

  grid-column: 2 / 5;
  grid-row: 1 / 2;
}

.welcome__title {
  z-index: 1;

  margin-top: 8px;

  font-weight: bold;
  font-size: 44px;
  line-height: 62px;
  color: #333333;

  grid-column: 2 / 8;
  grid-row: 2 / 3;
}

.welcome__text {
  z-index: 1;

  margin-top: 12px;

  font-size: 22px;
  line-height: 34px;
  color: #333333;

  grid-column: 2 / 8;
  grid-row: 3 / 4;
}

.welcome__action {
  width: 204px;
  height: 52px;
  margin-top: 40px;
  padding: 16px 32px;

  font-weight: 500;
  line-height: 1.25;
  color: #ffffff;

  background: var(--accent-color);

  grid-column: 2 / 5;
  grid-row: 4 / 5;

  &:hover,
  &:active,
  &:focus {
    text-decoration: none;

    opacity: 0.8;
  }
}

// Social
.welcome-social {
  display: flex;
  align-self: center;
  height: 44px;

  grid-column: 2 / 5;
  grid-row: 5 / 6;
}

.welcome-social__item {
  margin-right: 73px;

  &:last-child {
    margin-right: 0;
  }
}

.welcome-social__link {
  display: block;
  width: 44px;
  height: 44px;

  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  filter: grayscale(1);

  &:hover,
  &:focus,
  &:active {
    filter: grayscale(0);
  }

