// Slider
.slider {
  position: relative;

  grid-column: 6 / -1;
  grid-row: 1 / -1;

  img {
    object-fit: cover;
    object-position: center;
  }
}

.slider__controls {
  position: absolute;
  bottom: 0;

  display: grid;
  width: 100%;
  height: 64px;

  grid-template-columns: auto auto 1fr;
}

.slider-button {
  width: 104px;
  height: 64px;
  padding: 21px 39px;

  color: #eaf2f6;

  background: #ffffff;
  border: none;
  cursor: pointer;

  &:hover,
  &:focus,
  &:active {
    color: var(--accent-color);
  }

  svg {
    width: 100%;
    height: 100%;

    stroke: currentColor;
  }

  &:first-child {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      top: 16px;
      left: 104px;
      z-index: 20;

      width: 2px;
      height: 32px;

      background-color: #eaf2f6;
    }
  }
}

.slider-counter {
  padding-right: 30px;

  font-size: 32px;
  line-height: 1.5;
  text-align: right;
  color: #ffffff;
}
