.place {
  padding-top: 99px;
}

.place__container {
  display: grid;
  padding-bottom: 75px;

  column-gap: 24px;

  grid-template-columns: repeat(12, 1fr);

  & > * {
    margin-bottom: 24px;

    grid-column: 5 / 9;
  }
}

.section-title {
  display: flex;
  align-items: center;
  margin-bottom: 60px;

  font-weight: 300;
  font-size: var(--fz);
  line-height: 1.25;
  color: var(--accent-color);

  .divider {
    width: 83px;
    height: 1px;
    margin: 0 9px;

    background-color: #06a9b3;
  }
}

.place__container {
  h3 {
    font-weight: bold;
    font-size: 32px;
    line-height: 1.5;
    color: var(--color);
  }

  .place-wide-img {
    grid-column: 2 / -2;
  }

  blockquote {
    margin-bottom: 33px;
    padding-top: 9px;
    padding-left: 28px;

    font-size: 22px;
    line-height: 40px;
    color: #333333;

    border-left: 2px solid var(--accent-color);

    grid-column: 4 / 10;
  }
}

.place-aside {
  font-weight: 300;
  font-size: 14px;
  line-height: 24px;

  &--right {
    grid-column: 10 / 12;
  }

  &--left {
    grid-column: 2 / 4;
  }
}

.place__action {
  width: 289px;
  height: 52px;
  margin-top: 8px;
  padding: 16px 32px;

  font-weight: 500;
  line-height: 1.25;
  color: #ffffff;

  background: var(--accent-color);

  &:hover,
  &:active,
  &:focus {
    text-decoration: none;

    opacity: 0.8;
  }
}
