.contacts {
  display: grid;

  column-gap: 24px;

  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: 230px auto;
}

.contacts__title,
.contacts__form {
  grid-column: 2 / 6;
}

.contacts__img {
  grid-column: 7 / 13;
  grid-row: 1 / -1;
}

.contacts__title {
  .section-title {
    padding-top: 100px;
  }

  h3 {
    margin-bottom: 40px;

    font-weight: bold;
    font-size: 32px;
    line-height: 1.5;
    color: var(--color);
  }
}

.contacts__form {
  margin-top: 14px;
}

.form {
  display: grid;

  gap: 10px 24px;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(5, auto);
}

.form__element {
  &:nth-child(1),
  &:nth-child(2),
  &:nth-child(3) {
    grid-column: span 2;
  }

  &:nth-child(4),
  &:nth-child(5) {
    grid-column: span 6;
  }
  &:nth-child(6),
  &:nth-child(7) {
    grid-column: span 3;
  }

  &:nth-child(8) {
    grid-column: span 6;
  }

  &.checkbox {
    margin-top: 20px;
  }

  input,
  select {
    width: 100%;
    height: 44px;
    padding: 0 16px;

    background: #ffffff;
    border: 1px solid #cfdce3;

    &:focus {
      outline: 2px solid var(--accent-color);
    }
  }

  label {
    font-weight: 300;
    font-size: 12px;
    line-height: 1;
  }
}

.form__submit {
  width: 155px;
  height: 44px;
  margin-top: 30px;
  padding: 11px 32px;

  font-weight: 500;
  line-height: 1.25;
  color: #ffffff;

  background: var(--accent-color);
  border: none;

  &:hover,
  &:active,
  &:focus {
    text-decoration: none;

    opacity: 0.8;
  }
}

.checkbox label {
  position: relative;

  display: flex;
  padding-left: 30px;

  font-weight: 300;
  font-size: 12px;
  line-height: 16px;
  color: #333333;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;

    display: block;
    width: 18px;
    height: 18px;

    border: 1px solid #cfdce3;
  }

  &::after {
    position: absolute;
    top: 3px;
    left: 3px;

    width: 12px;
    height: 12px;

    background: var(--accent-color);
  }
}

.checkbox input[type="checkbox"]:checked + label::after {
  content: "";
}
