// Base styles

*,
*::before,
*::after {
  box-sizing: border-box;
}

*:not(input):focus {
  outline: none;
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;

  border: 0;

  clip: rect(1px, 1px, 1px, 1px);
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;

  font-weight: 300;
  line-height: 2;
  font-family: var(--ff);
  color: var(--color);
}
